
* {
  box-sizing: border-box;
}
html, body {
  margin: 0;
  padding: 0;
}
body {
  height: 100vh;
}

.App {
  text-align: center;
  min-height: 100%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.tiles {
  display: flex;
  flex: 1;
  flex-wrap: wrap;
  justify-content: space-between;
}
.tile {
  display: flex;
  flex-direction: column;
  margin: 0 0 20px 0;
  max-width: 200px;
  box-shadow: 0 .5rem 1rem rgba(0,0,0,.2);
  text-decoration: none;
}
/* .tile a {
  text-decoration: none;
} */
.tile-title {
  padding: 0 2px;
  color: black;
}
.tile img {
  max-width: 95%;
}
.tile-footer {
  font-size: 0.9em;
  padding: 2px;
  margin-top: auto;
  background-color: rgb(245, 243, 243);
  color: rbg(40 40, 40);
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
